.selectPageBody {
    height: 90%;
    width: 100vw;

    display: flex;
    flex-direction: column;
    
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.cardPack {
    height: 50vh;
    width: calc((37.5vh + 2.5vh) * 4) ;
    /* background-color: white; */

    /* transform: translateX(20vh); */
    transform: translateX(20vh);
    transition: none;
    display: flex;
}

.oneCardPack1 {
    background-image: url("../assets/heartDeckThumbnail.png");
    height: 50vh;
    width: 37.5vh;
    background-repeat: no-repeat;
    background-size: cover;

    border-radius: 21px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    border: solid 3px #ffff;
    margin: 0 1.25vh 0 1.25vh;
}
