.selectbut {
    width: 60vw;
    height: 55px;
    margin-top: 60px;
  
    display: flex;
    align-items: center;
    justify-content: center;
  
    border-radius: 50px;
    border-color: black;
  
    background-color: white;
    font-family: AppleSDGothicNeo;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    text-decoration: none;
    color: black;
  }

  .disabled {
    background-color: gainsboro;
  }