.iPhone-14-Pro-Display-Shape {
  height: 100vh;
  width: 100vw;
  margin: 0px auto 0;

  background-image: url("../assets/BackGroundImage.png");
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.homeLogo {
  /* height: 170px; */
