  width: 22px;
  height: 24px;
  position: absolute;
  left: 40px;

  font-family: AppleSDGothicNeo;
  font-size: 49px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #fff;
}

