.homecardpic {
  position: relative;
  /* background-color: white; */
  width: 500px;
  height: 40vh;
  display: flex;
  justify-content: center;

  margin-top: 50px;
}

.card1 {
  background-image: url("../assets/heartDeckThumbnail.png");
  height: 30vh;
  width: 22vh;
  background-repeat: no-repeat;
  background-size: cover;
  transform: rotate(-10deg);
  border-radius: 21px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  border: solid 1px #979797;

  position: relative;
  left: 30px;
}

.card2 {
  background-image: url("../assets/freecardpack.png");
  height: 33vh;
  width: 25vh;
  background-repeat: no-repeat;
  background-size: cover;
  transform: rotate(10deg);
  border-radius: 21px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  border: solid 1px #979797;

  position: relative;
  right: 20px;
  top: 10px;
}

.buttonbox {
  height: auto;
  /* background-color: white; */
}

.homebut {
  width: 300px;
  height: 55px;
  margin-top: 3vh;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: white;
  border-radius: 50px;
  border-color: black;

  font-family: AppleSDGothicNeo;
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  text-decoration: none;
  color: black;
}