.handPageBody {
    height: 90%;
    width: 100vw;

    display: flex;
    flex-direction: column;
    
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.handCardPack {
    height: 55vh;
    width: calc((37.5vh + 2.5vh) * 6) ;
    /* background-color: white; */

    /* transform: translateX(20vh); */
    transform: translateX(60vh);
    transition: none;
    display: flex;
}

.oneHandCardPack1 {
    background-image: url("../assets/card1.png");
    height: 55vh;
    width: 37.5vh;
    background-repeat: no-repeat;
    background-size: cover;

    border-radius: 21px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    /* border: solid 3px #ffff; */
    margin: 0 1.25vh 0 1.25vh;
}

